.concept {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
  background: linear-gradient(#2a3f3f, #467750);
  padding: 1.5rem;
  border-radius: 6px;
  box-shadow: 0 2px 20px rgba(240,205,110,.2);
  width: 33%;
  transition: all 0.2s;
}

.concept:hover {
  transform: translateY(-2px);
  transition: transform 0.2s;
}

.concept img {
  width: 6rem;
  height: 5rem;
  object-fit: contain;
}

.concept h2 {
  margin: 0;
  font-size: 1rem;
  text-transform: uppercase;
}

.concept p {
  margin: 0;
  text-align: center;
  font-size: 0.85rem;
}
